*
{
  margin: 0;
  padding: 0;
  font-family: "verdana", cursive, sans-serif;
  color: black;
}
body{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
}

.header{
  color: rgb(215, 101, 120);
  text-align: center;
}
.app{
  background: linear-gradient(5deg, #ff961d 0%, #f71aa2 100%);
  width: 95%;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}